body {
  background-color: #4177a1;
  font-family: "Aleo", sans-serif;
}

.move {
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
}
.favorites {
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
  background-color: #eff0d1;
  margin-top: 10px;
  margin-right: 40px;
  border-radius: 30px;
  padding: 16px 30px;
  text-decoration: none;
  box-shadow: 0 0 5px black;
}

.favorites:hover,
.favorites:active {
  background-color: #e6e990;
}
.favorites:active {
  padding: 12px 12px;
}

.form {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 70vh;
}

.input {
  height: 30px;
  border-radius: 5px;
  border: 1px solid black;
  border-left: 5px;
  box-shadow: 0 0 7px black;
}

.button {
  padding: 5px 10px;
  border: 1px solid black;
  border-radius: 5px;
  margin-left: 20px;
}

.button:active {
  padding: 8px 13px;
}
.button:focus {
  color: black;
}
.route {
  display: flex;
  justify-content: center;
  align-items: center;
}
